import React, { Fragment, useEffect, useState } from "react";
import Media from "components/Media/Media";
import Linkify from "react-linkify";
import { ReactTinyLink } from 'react-tiny-link'
import "../../assets/css/react-tiny-link-style.css";
import { useSelector } from "react-redux";
import maps from '../../assets/img/maps-social-share.png';

const ChatMessage = ({ avatar, title, message, id, event }) => {

    const [pressTime, setPressTime] = useState(0);
    const [intervalObject, setIntervalObject] = useState(undefined);
    const { messagefontSize } = useSelector(state => state.login);

    useEffect(() => {
        if (event) {
            event();
        }
    }, [event])

    useEffect(() => {
        if (pressTime >= 2) {
            console.log("long press");
            if (event) {
                event(id, intervalObject);
            } else {
                console.log("event not exist");
            }
        }
    }, [pressTime])

    const mouseDown = () => {
        console.log("on Mouse Down");
        let _pressTime = 0
        const t = setInterval(() => {
            _pressTime = _pressTime + 1;
            console.log(_pressTime);
            setPressTime(_pressTime);
        }, 1000);
        setIntervalObject(t);
        return false;
    }

    const mouseUp = () => {
        console.log("on Mouse Up");
        clearInterval(intervalObject);
        setPressTime(0);
        return false;
    }

    const chatFontSize = {
        fontSize: messagefontSize ? messagefontSize : "0.7rem",
        wordBreak: "break-word",
    }

    return (
        <Media
            avatar={avatar}
            title={
                <span style={chatFontSize}>
                    {title}
                </span>
            }
            body={
                <span>
                    <p style={chatFontSize} onMouseDown={mouseDown} onMouseUp={mouseUp}>
                        <Linkify componentDecorator={componentDecorator}>
                            {message}
                        </Linkify>
                    </p>
                </span>
            }
        />
    )
}

const componentDecorator = (href, text, key) => {

    const isGoogleMap = (href) => {
        let result = false;
        if (href.indexOf('goo.gl/maps') !== -1) {
            result = true;
        }

        if (href.indexOf('maps.app.goo.gl') !== -1) {
            result = true;
        }

        if (href.indexOf('www.google.com/maps') !== -1) {
            result = true;
        }

        return result;
    }

    const renderLink = () => {
        if (href && href !== "") {
            const check = href.indexOf('firebasestorage');
            if (check !== -1) {
                return (<></>)
            } else {
                return (
                    <a href={href} key={key} target="_blank" >
                        {text}
                    </a>
                )
            }
        } else {
            return (<></>)
        }
    }

    const renderLinkContent = () => {
        if (href && href !== "") {
            const check = href.indexOf('firebasestorage');
            if (check !== -1) {
                return (
                    <div style={{ fontSize: "12px", margin: "10px 0px" }}>
                        <ReactTinyLink
                            cardSize="large"
                            showGraphic={true}
                            header={"images"}
                            description={" "}
                            maxLine={1}
                            minLine={1}
                            url={href}
                        />
                    </div>
                )
            } else {
                if (!isGoogleMap(href)) {
                    return (
                        <div style={{ fontSize: "12px", margin: "10px 0px" }}>
                            <ReactTinyLink
                                cardSize="large"
                                showGraphic={true}
                                maxLine={2}
                                minLine={1}
                                url={href}
                            />
                        </div>
                    )
                } else {
                    return (
                        <>
                            <div style={{ fontSize: "12px", margin: "10px 0px" }}>
                                <ReactTinyLink
                                    cardSize="large"
                                    showGraphic={true}
                                    url={href}
                                    maxLine={2}
                                    minLine={1}
                                    defaultMedia={maps}
                                    header="Google Maps"
                                    description={href}
                                />
                            </div>
                        </>
                    )
                }
            }
        } else {
            return (<></>)
        }
    }



    return (
        <Fragment>
            {renderLink()}
            {renderLinkContent()}
        </Fragment>
    )
};

const PreviewMe = () => {
    return (<Fragment></Fragment>)
}





export default ChatMessage;